.container
{
    display:flex;
    flex-direction: row;
    justify-content: left;
    text-align: left;
    line-height: 1.4;
    padding-top: 1.5rem;
}

.amount
{
    text-align: center;
    width: 1.4rem;
    height: 1.4rem;
    padding-left: 0.4rem;
    padding-right: 0.4rem;
    border-radius: 50%;
    box-shadow: inset 0 0 4px 2px rgba(0,0,0,0.25);
    background-color: rgb(200, 16, 43);
    border-color: white;
    border-width: 1px;
    border-style: solid;
}


.name{
    margin-left: 0.5rem;
    width: 80%;
}
.price{
    position: relative;
    right: 0;
}

.control{
    display:flex;
    flex-direction: row;
    justify-content: space-between;
}

.control button{
    font-size: larger;
    width: 2rem;
    height: 2rem;
    text-align: center;
    border-radius: 50%;
    color: white;
    border-color: white;
    background-color:rgb(200, 16, 43);
    margin-left: 0%;
    margin-top:0.5rem;
}
.control button:hover{
    border-color: rgb(200, 16, 43);
    color: rgb(200, 16, 43);
    background-color: white;
}

